<template>
  <div id="app" :style = '{width:"100%",minHeight:winWH.height}'>

     <Top style='{position: "fixed";top: 0;z-index:2000;}'></Top>

    <!-- 缓冲层-->
    <buffering ></buffering>
    <!-- 提交层-->
    <Doing></Doing>
    <!-- <PopupForReward></PopupForReward> -->
    <!-- <popupForBidding></popupForBidding> -->
    <!-- <Popup></Popup> -->
    <!-- 顶部导航栏 -->
    <!-- <NavBar></NavBar> -->
    <!-- 视口 -->
    <keep-alive>
      <router-view name='com' v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>

    <router-view name='com' v-if="!$route.meta.keepAlive"></router-view>


    <!-- 底部标签栏 -->
    <!-- <TabBar></TabBar> -->
  </div>
</template>

<script>
  import {createNamespacedHelpers} from 'vuex'
  const login=createNamespacedHelpers('login')
  const win=createNamespacedHelpers('win')

  import buffering from "@/components/utils/buffering"
  import Doing from "@/components/utils/Doing"
  // import {mapState,mapMutations} from 'vuex'
  /* 引入标签栏组件*/
  // import Popup from "@/components/utils/Popup"
  // import PopupForReward from "@/components/utils/PopupForReward"
  // import popupForBidding from "@/components/utils/popupForBidding"
  /* 引入导航栏组件*/
  // import NavBar from "@/components/utils/NavBar"
  import Top from "@/components/utils/Top"

  /* 引入标签栏组件*/
  // import TabBar from "@/components/utils/TabBar"
export default {
  name: 'App',
  methods : {
    ...win.mapMutations({
          	winWHHandler: 'winWHHandler'
          }),
    /* 修改用户信息方法*/
    ...login.mapMutations({
            setHandler: 'setHandler'
          }),
  },
  components : {
  	/* 这个组件名字不能和原生标签名重复 */
    // TabBar,
    // NavBar,
    // Popup,
    // PopupForReward,
	// popupForBidding,
  Top,
  buffering,
  Doing,

  },
  sockets:{
    connect(){
      console.log('连接成功')
    },
    //重连成功 // 断开服务器端自动执行
    reconnect(){
      console.log('重连成功')
    },
    // 重连中...... 断开服务器端自动执行
    reconnecting(){
      console.log('重连中......')
    },
    //断开连接
    disconnect(data){
      // this.$socket.io.opts.query = {
      //     token:'fgh'
      //    }
      console.log('连接断开',this.$socket)
    },
    //自定义///////////////////////////////////////////////
    /* 有新消息*/
    newInfo(data){
      this.setHandler(['user_unread','add'])
      switch(data){
        case 'commentForComment':
          this.$toast('有人评论了您的评论')
        break;
        case 'commentForArticle':
          this.$toast('有人评论了您的文章')
        break;
        case 'reward':
          this.$toast('有人打赏了您的文章')
        break;
      }
    },
    /* 非法*/
    illegal(data){
      /* 中断socket连接*/
      this.$toast('中断socket连接')
      this.$socket.disconnect()
    }
    // error(data){
    //   console.log("io错误信息:",err);
    // }
  },
  computed:{
    ...win.mapState({
         	winWH : state => state.winWH
         }),
    _plus:{//保存plus对象
      get () {
            return this.$store._modulesNamespaceMap['login/'].context.state._plus
      },
      set (value) {
        this.$store._modulesNamespaceMap['login/'].context.commit('_plusHandler', value)
      }
    },
	...login.mapState({
	     	userInfo : state => state.userInfo
	     }),

  },
  beforeDestroy() {
    // 移除窗口大小事件
   window.removeEventListener('resize', this.winWHHandler)
  },
  mounted() {
    // 先执行窗口函数
    this.winWHHandler()
      // 绑定窗口大小事件
    window.addEventListener('resize', this.winWHHandler)
    /* *********************************************/
    var vueThis = this;
    document.addEventListener('plusready', function(a) { //等待plus对象 ready后再调用5+ API：
                vueThis._plus = plus;//将plus对象保存给vuex 供其他页面使用
                plus.navigator.setStatusBarBackground("rgb(23,200,98)");/* h5+ 设置移动端系统状态栏颜色*/
                var first = null;//定义一个变量保存前后两次触发的物理返回按键的时间戳
                plus.key.addEventListener('backbutton', function() { //监听返回键
                        if (!first) {//首次按键，提示‘再按一次退出应用’
                              console.log(vueThis.$route.path,'this.$route.path')
                              if(vueThis.$route.path == '/'){//如果当前在首页
                                first = new Date().getTime(); //获取第一次点击的时间戳
                                plus.nativeUI.toast("双击退出", {duration:'short'}); //通过H5+ API 调用Android 上的toast 提示框
                                setTimeout(function() {
                                    first = null;
                                }, 1000);
                              }else{//如果当前不在首页
                                var ws = plus.webview.getWebviewById('wv')//read页的webView
                                console.log('webview---------: '+ ws)
                                if(ws){//点击了webView
                                  ws.canBack( function(e){//检查当前webView的当前页 后面有没有页面
                                  		console.log('是否可返回：'+e.canBack);
                                      if(e.canBack){//后面有页面 可以返回
                                        ws.back();
                                      }else{//后面没有页面了 不可以返回
                                         plus.webview.close(ws);
                                      }
                                  	});
                                }else{//不在首页
                                  vueThis.$router.back(-1);
                                }
                              }
                        } else {//第二次按物理退出键
                            if (new Date().getTime() - first < 1000) { //获取第二次点击的时间戳, 两次之差 小于 1000ms 说明1s点击了两次,
                                plus.runtime.quit(); //退出应用
                            }
                        }
                    }, false);
            });
    /* *********************************************/
  },
  watch:{
    'userInfo.user_id': {
        handler(newVal, oldVal) {
          if(!!newVal == true){
            console.log(newVal,'用户id--------------')
            this.$socket.io.opts.query = {
                // token:newVal
                token : localStorage.getItem("token") || '没有token'
               }
            this.$socket.open()
          }

        },
        // immediate: true,
        // deep: true
    }
  }
}
</script>

<style scoped lang="scss">
#app{
  margin-top: 100px;
}
  // @import "./assets/fontSelf/fontSelf.css";
</style>



